import React, { useCallback, useState } from "react";
import { Dimensions, Platform, StyleSheet } from "react-native";
import { Card, Image, Text, TouchableOpacity, View } from "react-native-ui-lib";
import { colors } from "../../assets/color/color";
import { image_iVay, image_kita } from "../../assets/images/images";
import { useFocusEffect } from "@react-navigation/native";
import { getLoanBriefs } from "../api/DebitApi";
import { handleShowDate, scale } from "../utlis/func";
import { t } from "i18next";

const { width } = Dimensions.get("screen")

const FileComponent = (props: any) => {
  const [data, setData] = useState<any>()
  useFocusEffect(useCallback(() => {
    (async () => {
      const dataApi = await getLoanBriefs(0, 3);
      setData(dataApi)
    })()
  }, []))


  const { navigation } = props;
  const renderFileItem = (item: any, idx: number) => {
    return (
      <Card
        key={idx}
        height={80}
        activeOpacity={1}
        marginR-20
        marginL-20
        marginT-10
        onPress={() => navigation.navigate('ContractDetail', { id: item.id, briefName: item.briefName })}
        enableShadow={Platform.OS === "ios" ? true : false}
        style={{ backgroundColor: "transparent" }}
      >
        <View style={{ flexDirection: "row", alignContent: "center", justifyContent: "flex-start", columnGap: 12 }}>
          <View style={{ height: "100%", alignItems: "center", justifyContent: "center" }}>
            <Image source={{ uri: item.avatarCustomer }} width={40} height={40} borderRadius={20} />
          </View>
          <View style={{ rowGap: 3 }}>
            <Card.Section content={[{ text: item.briefName, text70: true, grey10: true, color: colors.colorText }]} />
            <Card.Section content={[{ text: `${item.phone} - ${handleShowDate(item.creationDate, false, true)}`, style: { width: scale(width / 1.4), fontSize: scale(12), fontWeight: "400" }, color: colors.text }]} />
            <Card.Section content={[{ text: `Sản phẩm vay vốn: ${item.branch}`, color: colors.text, style: { width: scale(width / 1.4), fontSize: scale(12), fontWeight: "400" } }]} />
          </View>
        </View>
      </Card>
    )
  }

  return (
    <View style={{ width: "100%" }}>
      <View style={{ alignItems: "center", justifyContent: "space-around", flexDirection: "row", marginBottom: 10 }}>
        <Text style={{ color: colors.colorTitie, fontSize: 16, fontWeight: "600" }}>{t("home.bodyHome.myProfileList")}</Text>
        <TouchableOpacity onPress={() => props.navigation.navigate('Contract')}>
          <Text style={{ color: '#39AC6D', fontSize: 12, fontWeight: "600" }}>{t("home.bodyHome.more")}</Text>
        </TouchableOpacity>
      </View>
      <View style={{ paddingHorizontal: 10 }}>
        {data && data.map((item: any, idx: number) => renderFileItem(item, idx))}
      </View>
    </View>
  )
}

const styles = StyleSheet.create({
  text: {

  }
})

export default FileComponent;